import { createRouter, createWebHistory } from "vue-router";

const routes = [
  {
    path: "/",
    name: "Home",
    component: () => import("@/pages/MainPages/HomePage"),
    meta: {
      title: "bazzled",
      metaTags: [
        {
          name: "description",
          content:
            "Invest in youtubers and content creators",
        },
        {
          property: "og:description",
          content:
            "Invest in youtubers and content creators",
        },
      ],
    },
  },
  {
    path: "/contact",
    name: "Contact",
    component: () => import("@/pages/MainPages/ContactPage.vue"),
    meta: {
      title: "bazzled",
      metaTags: [
        {
          name: "description",
          content:
            "Contact bazzled for any questions, comments, or concerns",
        },
        {
          property: "og:description",
          content:
            "Contact bazzled for any questions, comments, or concerns",
        },
      ],
    },
  },
  {
    path: "/partner",
    name: "Partner",
    component: () => import("@/pages/MainPages/PartnerPage.vue"),
    meta: {
      title: "bazzled",
      metaTags: [
        {
          name: "description",
          content:
            "Partner with bazzled to raise money for your channel",
        },
        {
          property: "og:description",
          content:
            "Partner with bazzled to raise money for your channel",
        },
      ],
    },
  },
  {
    path: "/about",
    name: "About",
    component: () => import("@/pages/MainPages/AboutPage.vue"),
    meta: {
      title: "bazzled",
      metaTags: [
        {
          name: "description",
          content:
            "Bazzled about us. Our team, mission, and journey",
        },
        {
          property: "og:description",
          content:
            "Bazzled about us. Our team, mission, and journey",
        },
      ],
    },
  },
  {
    path: "/login",
    name: "Login",
    component: () => import("@/pages/AuthPages/LoginPage.vue"),
    meta: {
      title: "bazzled",
      metaTags: [
        {
          name: "description",
          content:
            "Login to use bazzled services",
        },
        {
          property: "og:description",
          content:
            "Login to use bazzled services",
        },
      ],
    },
  },
  {
    path: "/signup",
    name: "Sign up",
    component: () => import("@/pages/AuthPages/SignupPage.vue"),
    meta: {
      title: "bazzled",
      metaTags: [
        {
          name: "description",
          content:
            "Signup for an account at bazzled",
        },
        {
          property: "og:description",
          content:
            "Signup for an account at bazzled",
        },
      ],
    },
  },
  {
    path: "/partner-signup",
    name: "Partner sign up",
    component: () => import("@/pages/AuthPages/PartnerSignupPage.vue"),
    meta: {
      title: "bazzled",
      metaTags: [
        {
          name: "description",
          content:
            "Signup for a partner account at bazzled",
        },
        {
          property: "og:description",
          content:
            "Signup for a partner account at bazzled",
        },
      ],
    },
  },
  {
    path: '/:pathMatch(.*)*',
    name: "PageNotFound",
    component: () => import("@/pages/PageNotFound"),
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
